



































































import {
  computed, defineComponent, PropType, Ref,
} from '@vue/composition-api';
import {
  CheckboxButtonEditorControl, CheckboxButtonGroupEditorControl,
  CheckboxToggleEditorControl, ColorBoxEditorControl,
  ComboBoxEditorControl,
} from '@/app/editor/client/view/component/control';
import { TextDefaults, TextSettings } from '@/app/shared/client/type/settings/text-settings';
import { Color } from '@/app/shared/client/type/color';
import {
  calculateActualColorDefault,
  calculateActualFontDefault,
  calculateActualTextSizeDefault,
} from '@/app/editor/client/logic/defaults';
import { GlobalDesign } from '@/app/shared/client/type/tile/global';
import { SystemFont, GoogleFont } from '@/app/shared/client/type/font';
import {
  fontFamilyValue,
  fontKeyLabelList,
  fontWithFallback,
  getFontIfExists,
} from '@/app/shared/client/logic/font';
import useI18N from '@/app/shared/client/logic/i18n';

function useActive(
  emit: (event: string) => void,
): () => void {
  function onActiveChange(): void {
    emit('change-active');
  }

  return onActiveChange;
}

function useFontChangeHandler(
  settings: Readonly<Ref<Readonly<TextSettings>>>,
  emit: (event: string, arg: TextSettings) => void,
): (value?: SystemFont | GoogleFont) => void {
  function onFontChange(value?: SystemFont | GoogleFont): void {
    const newSettings = {
      ...settings.value,
      font: value,
    };
    emit('editor-action', newSettings);
  }

  return onFontChange;
}

function useSizeChangeHandler(
  settings: Readonly<Ref<Readonly<TextSettings>>>,
  emit: (event: string, arg: TextSettings) => void,
): (value?: number) => void {
  function onSizeChange(value?: number): void {
    const newSettings = {
      ...settings.value,
      size: value,
    };
    emit('editor-action', newSettings);
  }

  return onSizeChange;
}

function useBoldChangeHandler(
  settings: Readonly<Ref<Readonly<TextSettings>>>,
  emit: (event: string, arg: TextSettings) => void,
): (value?: boolean) => void {
  function onBoldChange(value?: boolean): void {
    const newSettings = {
      ...settings.value,
      bold: value,
    };
    emit('editor-action', newSettings);
  }

  return onBoldChange;
}

function useItalicChangeHandler(
  settings: Readonly<Ref<Readonly<TextSettings>>>,
  emit: (event: string, arg: TextSettings) => void,
): (value?: boolean) => void {
  function onItalicChange(value?: boolean): void {
    const newSettings = {
      ...settings.value,
      italic: value,
    };
    emit('editor-action', newSettings);
  }

  return onItalicChange;
}

function useColorChangeHandler(
  settings: Readonly<Ref<Readonly<TextSettings>>>,
  emit: (event: string, arg: TextSettings) => void,
): (value?: Color) => void {
  function onColorChange(value?: Color): void {
    const newSettings = {
      ...settings.value,
      color: value,
    };
    emit('editor-action', newSettings);
  }

  return onColorChange;
}

function useVisibleChangeHandler(
  settings: Readonly<Ref<Readonly<TextSettings>>>,
  emit: (event: string, arg: TextSettings) => void,
): (value?: boolean) => void {
  function onVisibleChange(value?: boolean): void {
    const newSettings = {
      ...settings.value,
      visible: value,
    };
    emit('editor-action', newSettings);
  }

  return onVisibleChange;
}

const TextDesignEditor = defineComponent({
  components: {
    CheckboxButtonEditorControl,
    CheckboxButtonGroupEditorControl,
    CheckboxToggleEditorControl,
    ColorBoxEditorControl,
    ComboBoxEditorControl,
  },
  props: {
    value: {
      required: false,
      default: undefined,
      type: Object as PropType<TextSettings>,
    },
    defaults: {
      required: true,
      type: Object as PropType<TextDefaults>,
    },
    siteSettings: {
      required: false,
      default: undefined,
      type: Object as PropType<GlobalDesign>,
    },
    textSizeList: {
      required: true,
      type: Array as PropType<number[]>,
    },
    colorList: {
      required: true,
      type: Array as PropType<string[]>,
    },
    autoColorEnabled: {
      required: true,
      type: Boolean,
    },
    alphaColorEnabled: {
      required: true,
      type: Boolean,
    },
    hideVisibleToggle: {
      required: false,
      default: false,
      type: Boolean,
    },
  },
  setup(props, { root, emit }) {
    const i18n = useI18N(root.$store);

    const settings = computed(() => ({
      font: props.value?.font,
      size: props.value?.size,
      bold: props.value?.bold ?? props.defaults.bold,
      italic: props.value?.italic ?? props.defaults.italic,
      color: props.value?.color,
      visible: props.value?.visible ?? props.defaults.visible,
    }));
    const onActiveChange = useActive(emit);

    const font = computed({
      get: () => getFontIfExists(settings.value.font),
      set: useFontChangeHandler(settings, emit),
    });

    const fontChoices = computed(() => {
      const defaultValue = calculateActualFontDefault(
        props.defaults.font,
        props.siteSettings,
      );
      const defaultFontText = i18n('CommonLabels.Text.Font.Default')
        .replace('{fontName}', fontFamilyValue(fontWithFallback(defaultValue)) ?? '');

      return [
        { key: undefined, label: defaultFontText },
        ...fontKeyLabelList(),
      ];
    });

    const size = computed({
      get: () => settings.value.size,
      set: useSizeChangeHandler(settings, emit),
    });

    const sizeChoices = computed(() => {
      const defaultValue = calculateActualTextSizeDefault(
        props.defaults.size,
        props.siteSettings,
      );
      const defaultSizeText = i18n('CommonLabels.Text.Size.Default')
        .replace('{sizeInPixels}', defaultValue?.toString() ?? '');
      const baseSizeChoices = props.textSizeList
        .map((textSize) => ({ key: textSize, label: textSize.toString() }));

      return [
        { key: undefined, label: defaultSizeText },
        ...baseSizeChoices,
      ];
    });

    const bold = computed({
      get: () => settings.value.bold,
      set: useBoldChangeHandler(settings, emit),
    });

    const italic = computed({
      get: () => settings.value.italic,
      set: useItalicChangeHandler(settings, emit),
    });

    const defaultColor = computed(() => calculateActualColorDefault(
      props.defaults.color,
      props.siteSettings,
    ));
    const color = computed({
      get: () => settings.value.color,
      set: useColorChangeHandler(settings, emit),
    });

    const visibility = computed({
      get: () => settings.value.visible,
      set: useVisibleChangeHandler(settings, emit),
    });

    return {
      i18n,
      onActiveChange,
      bold,
      italic,
      defaultColor,
      color,
      size,
      sizeChoices,
      font,
      fontChoices,
      visibility,
    };
  },
});

export default TextDesignEditor;
